$easeOutExpo: cubic-bezier(0.16, 1, 0.3, 1);

@font-face {
  font-family: 'Cafe24Ssurround';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24Ssurround.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Cafe24ClassicType-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2210-2@1.0/Cafe24ClassicType-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

html {
  font-family: 'Cafe24ClassicType-Regular'
}

body {
  overflow-x: hidden;
  overflow-y: hidden;
  cursor: none;
  margin: 0;
  padding: 0;
  display-scrollbar: none;
}

body ::-webkit-scrollbar {
  scrollbar-width: 0;
}

.cursor {
  position: absolute;
  width: 50px;
  height: 50px; border-radius:50%;
  border: 2px solid #ccc; z-index: 500; backdrop-filter: blur(5px);
}

.prompt-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  backdrop-filter: blur(5px) brightness(50%);
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}

p {
  font-family: 'Cafe24Ssurround';
  color: #333;
}

button {
  display: flex;
  font-family: 'Cafe24Ssurround';
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  padding: 20px;
  width: 200px;
  border: none;
  border-radius: 20px;
  cursor: none;
  pointer-events: none;
  transition: box-shadow 0.5s $easeOutExpo;
  &:hover {
    box-shadow: 0px 5px 15px 5px rgba(0,0,0,0.3);
  }
}